import classNames from 'classnames';
import React, { Fragment } from 'react';
import Button from '../Button';
import SubTitle from '../SubTitle';
import Text from '../Text';
import Heading from '../Heading';
import TextBody from '../TextBody';

interface ActionItemType {
    icon: JSX.Element;
    key: string;
    label: string;
    path?: string;
    type: string;
}

/* interface StatisticsCardItem {
    count: number;
    header: string;
    description: string;
    actions: ActionItemType[];
}

interface CardMenu {
    icon: JSX.Element;
    title: string;
    description: string;
} */

export interface Props {
    type: 'Custom' | 'CardIcon' | 'CardAction' | 'CardContent';
    details: any;
    content?: JSX.Element;
    handleClick?: (item: any) => void;
}

export const Card = (props: Props): JSX.Element => {
    const { content, type, details } = props;
    const getCardContentByType = (type: string): JSX.Element => {
        switch (type) {
            case 'CardIcon': {
                if (details) {
                    const { icon, title, description, handleClick } = details;
                    return (
                        <div
                            onClick={(): void => {
                                if (handleClick) {
                                    handleClick(details);
                                }
                            }}
                            className='card-menu'
                        >
                            <div className='card-item'>
                                <div className='icon'>{icon}</div>
                                <div className='content'>
                                    <SubTitle size={1} text={title} />
                                    <Text type='caption' text={description} />
                                </div>
                            </div>
                        </div>
                    );
                }
                return <Fragment />;
            }
            case 'CardAction': {
                if (details) {
                    const { count, header, description, actions, handleClick } = details;
                    const countClass = classNames('card-item', {
                        zeroclass: count === 0,
                    });
                    const btnActions: ActionItemType[] | undefined =
                        count > 0
                            ? actions
                            : actions?.filter((action: ActionItemType) => action.type !== 'edit');

                    return (
                        <div className='card-stats'>
                            <div className={countClass}>
                                <Heading size={3} text={count} />
                                <Heading size={6} text={header} />
                                <TextBody size={2} text={description} />
                            </div>
                            <Fragment>
                                {btnActions && btnActions.length > 0 && (
                                    <div className='card-actions'>
                                        {btnActions.map(
                                            (actionItem: ActionItemType, index: number) => {
                                                const { key, label, icon } = actionItem;
                                                return (
                                                    <div
                                                        key={`home-action-btn-${index}-${key}`}
                                                        className='card-btn-wrapper'
                                                    >
                                                        <Button
                                                            id={key}
                                                            label={label}
                                                            startIcon={icon}
                                                            onClick={(): void => {
                                                                if (handleClick) {
                                                                    handleClick(actionItem);
                                                                }
                                                            }}
                                                            variant='text'
                                                            dense={40}
                                                        />
                                                    </div>
                                                );
                                            }
                                        )}
                                    </div>
                                )}
                            </Fragment>
                        </div>
                    );
                }
                return <Fragment />;
            }
            case 'CardContent': {
                const { icon, header, description, actions, handleClick } = details;
                return (
                    <div className='card-content'>
                        <div className='card-icon'>{icon}</div>
                        <div className='card-title-description'>
                            <div className='card-title'>
                                <SubTitle size={2} text={header} />
                            </div>
                            <div className='card-description'>
                                <TextBody size={2} text={description} />
                            </div>
                        </div>
                        {actions && actions.length > 0 && (
                            <div className='card-actions'>
                                {actions.map((action: any) => {
                                    const { icon, label, type } = action;
                                    return (
                                        <div
                                            key={`card-action-${action.key}`}
                                            className={`action-button ${type}`}
                                            onClick={(): void => {
                                                if (handleClick) {
                                                    handleClick(action);
                                                }
                                            }}
                                        >
                                            <div className='action-icon'>{icon}</div>
                                            <div className='action-label'>{label}</div>
                                        </div>
                                    );
                                })}
                            </div>
                        )}
                    </div>
                );
            }
            case 'Custom':
            default:
                return <Fragment>{content}</Fragment>;
        }
    };
    return <div id='RRUCCard'>{getCardContentByType(type)}</div>;
};

export default Card;
