import React from 'react';
import classNames from 'classnames';

/**
 * * description: optional srting for list element
 * * icon: icon for list item
 *
 */
export interface ListItemType {
    description?: string;
    icon: JSX.Element;
    path?: string;
    title: string;
}

export interface Props {
    handleMenuClick?: (selected: ListItemType) => void;
    items: Array<ListItemType> | undefined;
    selectedItem?: string;
}

export const ListItems = (props: Props): JSX.Element => {
    const { items, selectedItem, handleMenuClick } = props;
    const handleSelectedMenuItem = (selected: ListItemType): void => {
        if (handleMenuClick) {
            handleMenuClick(selected);
        }
    };
    return (
        <div id='RRUCListItem'>
            {items &&
                items.length > 0 &&
                items.map((item, index) => {
                    const { icon, title } = item;
                    const listItem = classNames('list-item', {
                        active: selectedItem === title,
                    });
                    return (
                        <div
                            key={`list-item-${index}`}
                            className={listItem}
                            onClick={(): void => handleSelectedMenuItem(item)}
                        >
                            <div className='list-icon'>{icon}</div>
                            <div className='list-title'>{title}</div>
                        </div>
                    );
                })}
        </div>
    );
};

export default ListItems;
