import React from 'react';
import { Formik, Form as FormikForm } from 'formik';
import { OutlinedTextFieldProps } from '@material-ui/core';

import Button from '../Button';
import TextField, { Option } from '../TextField';
import GridContainer, { BreakPointType } from '../GridContainer';
import Upload from '../Upload';

/**
 * * colcss: optional string applied for wrapper to the element
 * * format: optional string used for textinput for showing the different formats, ex: +87 ##### ##
 * * helperText:  optional string which will be shown below the field element
 * * name: string which used as unique key for the form field element
 * * options: for display as drop down elements
 * * type: string used to display different form field based on this property, type can be ex TextField, Select etc.,
 */
export interface FormListItemType extends OutlinedTextFieldProps {
    colcss?: string;
    format?: string;
    helperText?: string;
    name: string;
    options?: Array<Option>;
    type: string;
    handleClick?: () => void;
}

/**
 * * cols: list of form field elements
 * * sizes: breakpoints to display form elements in responsive mode
 */
export interface FormListType {
    cols: FormListItemType[] | any[];
    sizes?: BreakPointType;
}

/**
 * * formList: FormListType[];
 * *   handleFormSubmit: call back event to the parent, which will pass the modified values and actions to the parent
 * *   helperText?: optional string display on the form
 * *   initialValues: default values for the form
 * *   name: unique name for the form
 * *   schema: object with validation rules
 * *   submitBtnText: string to be displayed on the primary button
 */
export interface Props {
    formList: FormListType[];
    handleFormSubmit: (values: object, actions: object) => void;
    helperText?: string;
    initialValues: object;
    name: string;
    schema?: object;
    submitBtnText: string;
}

/**
 *
 * @param props
 * based on cols arrange the positoning of elements and based on schema do the validation
 * on submit click send back the form values
 * @returns  Form Element based on the schema validation and positions
 */
export const Form = (props: Props): JSX.Element => {
    const { name, initialValues, schema, handleFormSubmit, formList, submitBtnText } = props;

    /**
     *
     * @param errors : object contains all the error values
     * @param touched : field is pristine or not
     * @param key : unique identification of the element
     * @returns : boolean true or false based on the validation schema rule
     */
    const isError = (errors: object, touched: object, key: string): boolean => {
        return errors[key] && touched[key];
    };

    /**
     *
     * @param errors : object contains all the error values
     * @param touched : field is pristine or not
     * @param key : unique identification of the element
     * @param element : form field element
     * @returns : validation message if any, else returns helpertext if passed else empty string
     */
    const getMessage = (
        errors: object,
        touched: object,
        key: string,
        element: FormListItemType
    ): string => {
        if (isError(errors, touched, key)) {
            return errors[key];
        } else if (element.helperText) {
            return element.helperText;
        } else {
            return '';
        }
    };

    /**
     *
     * @param element : Element properties ex: id, label, inputProps etc..,
     * @param values : list which contains the form field values
     * @param errors : form error objects
     * @param touched : form elements touched object
     * @param handleChange : function to update the values for the field
     * @returns: form element like <TextField>, <Select>
     */
    const getElementByType = (
        element: FormListItemType,
        values: any,
        errors: object,
        touched: object,
        handleChange: any
    ): JSX.Element => {
        const { type, colcss, name, handleClick } = element;
        switch (type) {
            case 'Upload':
                return (
                    <Upload
                        handleClick={() => {
                            if (handleClick) {
                                handleClick();
                            }
                        }}
                    />
                );
            case 'TextField':
            case 'password':
            default:
                return (
                    <div className={colcss}>
                        <TextField
                            {...element}
                            value={values[name]}
                            error={isError(errors, touched, name)}
                            helperText={getMessage(errors, touched, name, element)}
                            onChange={handleChange}
                        />
                    </div>
                );
        }
    };
    /**
     *
     * @param cols : for each row multiple column fields
     * @param values : list of form field values
     * @param errors : form errors object
     * @param touched : form touched object
     * @param handleChange : callback function to update the form field value
     * @returns
     */
    const getElement = (
        cols: FormListItemType[],
        values: any,
        errors: object,
        touched: object,
        handleChange: any
    ): JSX.Element[] => {
        return cols.map((element: FormListItemType) => {
            return getElementByType(element, values, errors, touched, handleChange);
        });
    };
    /**
     * @return <form /> component with validation and responsive positioning
     */
    return (
        <div id='RRUCForm'>
            <Formik
                initialValues={initialValues}
                validationSchema={schema}
                onSubmit={(values, actions): void => {
                    handleFormSubmit(values, actions);
                }}
            >
                {(props: any): JSX.Element => {
                    const { values, errors, touched, handleChange } = props;
                    return (
                        <div className='page-form'>
                            <FormikForm onSubmit={props.handleSubmit}>
                                {formList &&
                                    formList.length > 0 &&
                                    formList.map((formRow: FormListType, index: number) => {
                                        const { sizes, cols } = formRow;
                                        return (
                                            cols && (
                                                <GridContainer
                                                    spacing={2}
                                                    key={`form-${name}-${index}`}
                                                    name={name}
                                                    items={getElement(
                                                        cols,
                                                        values,
                                                        errors,
                                                        touched,
                                                        handleChange
                                                    )}
                                                    sizes={sizes}
                                                    containerCss='form-row'
                                                    itemCss='form-col-wrapper'
                                                />
                                            )
                                        );
                                    })}
                                <div className='page-form-actions'>
                                    <Button
                                        id='submitBtn'
                                        variant='filled'
                                        label={submitBtnText}
                                        dense={40}
                                        type='submit'
                                    />
                                </div>
                            </FormikForm>
                        </div>
                    );
                }}
            </Formik>
        </div>
    );
};

export default Form;
