import React from 'react';
import { Checkbox, CheckboxProps } from '@material-ui/core';

/**
 * * indeterminate: boolean to show check box as un selectable (neutral)
 * * text: string to be shown next to the checkbox
 */
export interface Props extends CheckboxProps {
    indeterminate?: boolean;
    text?: string | JSX.Element;
}

export const CheckBox: React.FC<Props> = ({ checked = false, text, ...props }: Props) => {
    return (
        <div id='RRUCCheckBox'>
            <Checkbox checked={checked} {...props} />
            {text && <div>{text}</div>}
        </div>
    );
};

export default CheckBox;
