import React from 'react';
import { Button as MDButton } from '@material-ui/core';
import classNames from 'classnames';

/**
 * * dense: height of the button, its a optional one, by default 32 will be the height of the button
 * * disabled: boolean which will be used to disable or enable button
 * * id: a unique string used as id
 * * label: string, used to display the string on the label
 * * onClick: evenvt to call back the parent functionality on click of a button
 * * startIcon: is the JSX.Element which is optional to show the button with icon if any passed
 * * type: type of the button can be submit, button and reset
 * * variant: different forms/look for the button based on the variant.
 */
export interface Props {
    dense?: 32 | 40 | 48;
    disabled?: boolean;
    id: string;
    label: string;
    onClick?: () => void;
    startIcon?: JSX.Element;
    type?: 'button' | 'submit' | 'reset' | undefined;
    variant: 'filled' | 'outlined' | 'text' | 'social-button' | 'link';
}

export const Button = ({
    variant = 'filled',
    label,
    startIcon,
    dense = 32,
    onClick,
    ...props
}: Props): JSX.Element => {
    const btnClass = classNames('btn', {
        'btn-filled': variant === 'filled',
        'btn-outlined': variant === 'outlined',
        'btn-text': variant === 'text',
        'btn-icon': variant === 'social-button',
        'action-button': variant === 'text' && startIcon,
    });
    /**
     * call the parent click function
     */
    const handleClick = (): void => {
        if (onClick) {
            onClick();
        }
    };
    /**
     * * if variant link then return custom span else return button with different variants as specified in material-ui
     */
    return (
        <div id='RRUCButton'>
            {variant !== 'link' && (
                <MDButton
                    className={`${btnClass} height-${dense}`}
                    startIcon={startIcon}
                    onClick={onClick}
                    {...props}
                >
                    {label}
                </MDButton>
            )}
            {variant === 'link' && (
                <span className='btn-link' onClick={(): void => handleClick()}>
                    {label}
                </span>
            )}
        </div>
    );
};

export default Button;
