import React, { Fragment } from 'react';
import Button from '../Button';

/**
 * * id: for unique identification for each breadcrumb item
 * * label: display string which will be shown on UI
 * * path: string contains the route where it will navigate on click
 */
export interface BreadCrumbType {
    id: string;
    label: string;
    path: string;
}
/**
 * * handleClick: optional props which call back event to pass the selected breadcrumb route path to the child
 */
export interface Props {
    handleClick?: (path: string) => void;
    paths: BreadCrumbType[];
}

/**
 * * Based on paths array return the breadcrumbs and set active all the links except current link
 */
export const BreadCrumbs = (props: Props): JSX.Element => {
    const { paths, handleClick } = props;
    return (
        <div id='RRUCBreadCrumbs'>
            {paths && paths.length > 0 && (
                <div className='breadcrumbs'>
                    {paths.map((pathItem: BreadCrumbType, index: number) => {
                        const { id, path, label } = pathItem;
                        return (
                            <Fragment key={`breadcrumb-${index}-${id}`}>
                                {index !== 0 && <span className='bradcrumbs-divider'>/</span>}
                                {paths.length - 1 !== index && (
                                    <Fragment>
                                        <Button
                                            id={id}
                                            variant='link'
                                            label={label}
                                            onClick={(): void => {
                                                if (handleClick) {
                                                    handleClick(path);
                                                }
                                            }}
                                        />
                                    </Fragment>
                                )}
                                {paths.length - 1 === index && (
                                    <Fragment>
                                        <div className='greyLink'>{label}</div>
                                    </Fragment>
                                )}
                            </Fragment>
                        );
                    })}
                </div>
            )}
        </div>
    );
};

export default BreadCrumbs;
